                    fullContent: "On Sunday, August 24, 2025, I will be hosting a workshop that I have planned and will guide, titled 'A Symbiotic Landscape Woven by Plants and People - Learning Plant Abilities Through Touch,' at the 'Playground of Life: Kurage Pavilion' of the Osaka-Kansai Expo.\n\nThis project is a participatory event where you can intuitively experience the surprising abilities of plants by seeing and touching them. The venue will feature five stations based on my research, including 'Mimosa Petting,' 'Nyctinasty Observation,' and 'Electrical Signal Visualization.' I hope to create a vision of a future where humans and plants coexist and co-create, by having participants discover the mysteries of plants as they freely tour these stations.\n\nWe sincerely look forward to your visit.\n\n[Event Details]\n Date & Time: August 24, 2025 (Sun) 10:15 - 13:15\n Location: Osaka-Kansai Expo 2025, Playground of Life: Kurage Pavilion 'Inochi no Yuragi-ba'\n How to Participate: No reservation required (first-come, first-served)\n\n*Please note that the event details are subject to change or cancellation depending on the situation.",
