                    fullContent: "On Sunday, September 28, 2025, I gave a presentation at the project outcome report meeting co-hosted by Rohto Pharmaceutical Co., Ltd. and Leave a Nest Co., Ltd., held at 'Grand Green Osaka' in front of Osaka Station.\n\nThis project, inspired by the Osaka-Kansai Expo, was aimed at enabling high school students to explore future societal challenges and create 'Future Research Themes.' As a team member, I spent over two months tackling the grand theme of 'Mental Illness in the Year 2100.'\n\nLeading up to the presentation, our team held numerous online discussions and also visited the Osaka-Kansai Expo in person to deepen our understanding of future medicine and society. On the day of the event, I was not only able to present the ideas we had developed but also engaged in lively discussions about the proposals from other teams, making it a very fulfilling experience.\n\nThrough this project, I became keenly aware of the applied science perspectivehow to connect the knowledge from my fundamental research, such as on Mimosa pudica and mathematical models, to real-world social implementation. This was a valuable experience that marks a significant turning point as I consider my future career as a researcher.\n\nI would like to express my sincere gratitude to everyone at Rohto Pharmaceutical Co., Ltd. and Leave a Nest Co., Ltd. for this wonderful opportunity, as well as to my teammates with whom I explored this theme, and to everyone else involved.",
