// ---  ---
const InteractiveIntro = ({ onFinish }) => {
    const [isClicked, setIsClicked] = useState(false);

    const handleClick = () => {
        if (isClicked) return;
        setIsClicked(true);
        setTimeout(onFinish, 2000); //  +  (3500ms)
    };

    const containerVariants = {
        exit: { 
            opacity: 0, 
            transition: { duration: 0.5, delay: 1.0 } // 3.0s
        }
    };

    const textVariants = {
        initial: { opacity: 1 },
        exit: { opacity: 0, transition: { duration: 0.3 } }
    };

    const stemVariants = {
        hidden: { pathLength: 0 },
        visible: {
            pathLength: 1,
            transition: { duration: 1.5, ease: "easeInOut", delay: 0.5 }
        }
    };

    const leafVariants = {
        hidden: { scale: 0, opacity: 0 },
        visible: {
            scale: 1,
            opacity: 1,
            transition: {
                duration: 1,
                ease: [0.22, 1, 0.36, 1], // A more "bouncy" ease
                delay: 1.5 // After stem is mostly grown
            }
        }
    };

    return (
        <motion.div
            key="intro"
            initial={{ opacity: 1 }}
            exit="exit"
            variants={containerVariants}
            className="fixed inset-0 z-[10000] flex flex-col items-center justify-center bg-emerald-50 cursor-pointer"
            onClick={handleClick}
        >
            <AnimatePresence>
                {!isClicked && (
                    <motion.div
                        variants={textVariants}
                        exit="exit"
                        className="text-center mb-4"
                    >
                        <p className="text-emerald-700 text-lg font-semibold"></p>
                        <p className="text-emerald-600 text-md">Click to Start</p>
                    </motion.div>
                )}
            </AnimatePresence>
            <svg width="250" height="250" viewBox="0 0 200 200">
                {/* Ground Line - always visible */}
                <path
                    d="M 60,180 H 140"
                    stroke="#a3bfb0"
                    strokeWidth="3"
                />

                {isClicked && (
                    <>
                        {/* Stem */}
                        <motion.path
                            d="M 100,180 C 100,130 80,90 100,60"
                            stroke="#34d399"
                            strokeWidth="4"
                            fill="none"
                            initial="hidden"
                            animate="visible"
                            variants={stemVariants}
                        />

                        {/* Two Leaves */}
                        <motion.g initial="hidden" animate="visible">
                            <motion.path
                                d="M 100,60 C 80,65 70,85 80,100"
                                fill="#6ee7b7"
                                variants={leafVariants}
                                style={{ transformOrigin: "100px 60px" }}
                            />
                            <motion.path
                                d="M 100,60 C 120,65 130,85 120,100"
                                fill="#6ee7b7"
                                variants={leafVariants}
                                style={{ transformOrigin: "100px 60px" }}
                            />
                        </motion.g>
                    </>
                )}
            </svg>
        </motion.div>
    );
};


